/* CSS Variables for consistent theming */
:root {
  --primary-color: #4F46E5;
  --secondary-color: #9333EA;
  --background-gradient: linear-gradient(to right, #add8e6, rgba(222, 246, 63, 0.736));
  --card-background: rgba(255, 255, 255, 0.8);
  --card-border: rgba(255, 255, 255, 0.2);
  --text-primary: #111827;
  --text-secondary: #6B7280;
  --shadow-default: 0 25px 15px rgba(238, 248, 169, 0.736);
  --shadow-hover: 0 10px 15px -3px rgba(0, 0, 0, 0.1);
  --button-primary: #007bff;
  --button-hover: #0056b3;
}

/* Base Styles */
.dashboard-container {
  display: flex;
  min-height: 100vh;
  background: var(--background-gradient);
  color: var(--text-primary);
  font-family: "Arial", sans-serif;
  box-shadow: var(--shadow-default);
  padding: 16px;
}

/* Sidebar Styles */
.dashboard-sidebar {
  width: 240px;
  height: 100vh;
  padding: 24px;
  background: rgba(255, 255, 255, 0.3);
  backdrop-filter: blur(12px);
  border-radius: 16px;
  margin-right: 16px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  display: flex;
  flex-direction: column;
}

.sidebar-header {
  display: flex;
  align-items: center;
  margin-bottom: 32px;
}

.sidebar-logo-icon {
  font-size: 24px;
  margin-right: 8px;
}

.sidebar-logo-text {
  font-size: 20px;
  font-weight: 700;
  background: linear-gradient(90deg, #4F46E5 0%, #9333EA 100%);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.sidebar-nav {
  display: flex;
  flex-direction: column;
  gap: 8px;
  flex-grow: 1;
}

.nav-item {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 12px;
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.nav-item:hover {
  background: rgba(255, 255, 255, 0.3);
  color: #4F46E5;
}

.nav-item.active {
  background: rgba(255, 255, 255, 0.5);
  color: #4F46E5;
  font-weight: 500;
}

.nav-item-icon {
  width: 20px;
  height: 20px;
}

.nav-item-label {
  font-size: 14px;
}

.sidebar-footer {
  padding: 16px;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  margin-top: auto;
}

.footer-text {
  font-size: 14px;
  font-weight: 500;
  margin-bottom: 4px;
}

.footer-subtext {
  font-size: 12px;
  color: #4B5563;
}

/* Main Content Styles */
.dashboard-main {
  flex: 1;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 16px;
  padding: 16px 0;
}

/* Card Styles */
.metric-card, .chart-card, .next-flight-card, 
.jet-showcase-card, .map-card, .chat-card, 
.inspiration-card {
  border-radius: 10px;
  background: var(--card-background);
  backdrop-filter: blur(12px);
  border: 1px solid var(--card-border);
  box-shadow: var(--shadow-default);
  padding: 20px;
  transition: box-shadow 0.3s ease;
}

.metric-card:hover, .chart-card:hover, .next-flight-card:hover, 
.jet-showcase-card:hover, .map-card:hover, .chat-card:hover, 
.inspiration-card:hover {
  box-shadow: var(--shadow-hover);
}

/* Metrics Grid */
.metrics-grid {
  grid-column: span 2;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 16px;
}

.metric-header {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 8px;
}

.metric-icon {
  width: 36px;
  height: 36px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.metric-title {
  font-size: 16px;
  font-weight: 600;
}

.metric-value {
  font-size: 28px;
  font-weight: 700;
  margin-bottom: 4px;
}

.metric-subtext {
  font-size: 12px;
  color: #6B7280;
}

/* Charts */
.chart-card {
  grid-column: span 2;
}

.chart-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
}

.chart-title {
  font-size: 18px;
  font-weight: 600;
}

.chart-dropdown {
  background: rgba(255, 255, 255, 0.5);
  border: 1px solid #E5E7EB;
  border-radius: 8px;
  padding: 6px 12px;
  font-size: 14px;
}

.chart-container {
  height: 256px;
}

.pie-chart-container {
  position: relative;
  height: 256px;
  width: 100%;
}

.pie-chart-center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

.pie-chart-percentage {
  font-size: 24px;
  font-weight: 700;
  color: #111827;
}

.pie-chart-label {
  font-size: 12px;
  color: #6B7280;
}

/* Middle Grid */
.middle-grid {
  grid-column: span 2;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 16px;
}

.next-flight-card {
  display: flex;
  align-items: center;
  justify-content: center;
}

.flight-icon {
  width: 48px;
  height: 48px;
  background: rgba(79, 70, 229, 0.1);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 16px;
}

.flight-title {
  font-size: 18px;
  font-weight: 600;
  text-align: center;
  margin-bottom: 8px;
}

.flight-route {
  font-size: 14px;
  font-weight: 500;
  text-align: center;
  margin-bottom: 4px;
}

.flight-time {
  font-size: 12px;
  color: #6B7280;
  text-align: center;
  margin-bottom: 16px;
}

.flight-button {
  composes: button-base;
}

/* Jet Showcase */
.jet-showcase-card {
  grid-column: span 2;
}

.showcase-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
}

.showcase-title {
  font-size: 18px;
  font-weight: 600;
}

.showcase-controls {
  display: flex;
  gap: 8px;
}

.control-button {
  width: 32px;
  height: 32px;
  background: rgba(255, 255, 255, 0.5);
  border: 1px solid #E5E7EB;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.jet-cards-container {
  display: flex;
  gap: 16px;
  overflow-x: auto;
  padding-bottom: 8px;
}

.jet-card {
  min-width: 280px;
  height: 192px;
  border-radius: 12px;
  position: relative;
  background-size: cover;
  background-position: center;
}

.gulfstream-bg {
  background-image: url('https://images.unsplash.com/photo-1556388158-158ea5ccacbd?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=1740&q=80');
}

.bombardier-bg {
  background-image: url('https://images.unsplash.com/photo-1578973615934-8d9cdb0c0c67?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=1632&q=80');
}

.dassault-bg {
  background-image: url('https://images.unsplash.com/photo-1581094794329-c811e6f706b4?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=1740&q=80');
}

.jet-card-overlay {
  position: absolute;
  inset: 0;
  background: linear-gradient(to top, rgba(0, 0, 0, 0.7) 0%, transparent 100%);
  border-radius: 12px;
}

.jet-card-content {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 16px;
  color: white;
}

.jet-card-title {
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 8px;
}

.jet-card-details {
  display: flex;
  justify-content: space-between;
  font-size: 12px;
  margin-bottom: 12px;
}

.jet-card-button {
  width: 100%;
  padding: 8px;
  background: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(4px);
  border-radius: 8px;
  color: white;
  font-size: 12px;
  font-weight: 500;
  transition: background 0.2s ease;
}

.jet-card-button:hover {
  background: rgba(255, 255, 255, 0.3);
}

/* Map Card */
.map-card {
  grid-column: span 2;
}

.map-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
}

.map-title {
  font-size: 18px;
  font-weight: 600;
}

.map-controls {
  display: flex;
  gap: 8px;
}

.map-container {
  height: 256px;
  background: linear-gradient(135deg, #E0F2FE 0%, #F0F9FF 100%);
  border-radius: 12px;
  position: relative;
  overflow: hidden;
  border: 1px solid rgba(255, 255, 255, 0.5);
}

.map-container::before {
  content: '';
  position: absolute;
  inset: 0;
  background-image: url('https://images.unsplash.com/photo-1635070041078-e363dbe005cb?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=1740&q=80');
  background-size: cover;
  background-position: center;
  opacity: 0.2;
}

.map-overlay {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 16px;
  background: rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(4px);
  border-radius: 8px;
  border: 1px solid rgba(255, 255, 255, 0.5);
  text-align: center;
}

.map-overlay-title {
  font-weight: 500;
  margin-bottom: 4px;
}

.map-overlay-subtitle {
  font-size: 12px;
  color: #6B7280;
}

/* Chat Card */
.chat-card {
  grid-column: span 2;
}

.chat-header {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 16px;
}

.chat-icon {
  width: 36px;
  height: 36px;
  background: rgba(79, 70, 229, 0.1);
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.chat-title {
  font-size: 18px;
  font-weight: 600;
}

.chat-messages {
  height: 192px;
  background: #F9FAFB;
  border-radius: 12px;
  padding: 16px;
  display: flex;
  flex-direction: column;
  gap: 12px;
  overflow-y: auto;
  border: 1px solid #E5E7EB;
}

.message {
  max-width: 80%;
  padding: 12px;
  border-radius: 12px;
  font-size: 14px;
}

.message.received {
  align-self: flex-start;
  background: white;
  border-bottom-left-radius: 4px;
}

.message.sent {
  align-self: flex-end;
  background: #4F46E5;
  color: white;
  border-bottom-right-radius: 4px;
}

.message-time {
  font-size: 10px;
  margin-top: 4px;
  opacity: 0.7;
}

.message.received .message-time {
  color: #6B7280;
}

.message.sent .message-time {
  color: rgba(255, 255, 255, 0.7);
}

.chat-input-container {
  display: flex;
  gap: 8px;
  margin-top: 16px;
}

.chat-input {
  composes: input-base;
}

.chat-send-button {
  composes: button-base;
}

/* Inspiration Card */
.inspiration-card {
  grid-column: span 2;
}

.inspiration-header {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 16px;
}

.inspiration-icon {
  width: 36px;
  height: 36px;
  background: rgba(251, 191, 36, 0.1);
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.inspiration-title {
  font-size: 18px;
  font-weight: 600;
}

.inspiration-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 16px;
}

.inspiration-item {
  height: 192px;
  border-radius: 12px;
  position: relative;
  background-size: cover;
  background-position: center;
}

.st-moritz-bg {
  background-image: url('https://images.unsplash.com/photo-1605540436563-5bca919ae766?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=1738&q=80');
}

.maldives-bg {
  background-image: url('https://images.unsplash.com/photo-1571896349842-33c89424de2d?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=1738&q=80');
}

.tokyo-bg {
  background-image: url('https://images.unsplash.com/photo-1492571350019-22de08371fd3?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=1753&q=80');
}

.inspiration-overlay {
  position: absolute;
  inset: 0;
  background: linear-gradient(to top, rgba(0, 0, 0, 0.7) 0%, transparent 100%);
  border-radius: 12px;
}

.inspiration-content {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 16px;
  color: white;
}

.inspiration-item-title {
  font-size: 16px;
  font-weight: 700;
  margin-bottom: 4px;
}

.inspiration-item-description {
  font-size: 12px;
  margin-bottom: 12px;
}

.inspiration-item-button {
  composes: button-base;
}

/* Responsive Adjustments */
@media (max-width: 1024px) {
  .dashboard-container {
    flex-direction: column;
    padding: 12px;
  }
  
  .dashboard-sidebar {
    width: 100%;
    height: auto;
    margin: 0 0 16px 0;
  }
  
  .dashboard-main {
    grid-template-columns: 1fr;
    gap: 12px;
  }
}

@media (max-width: 768px) {
  .dashboard-container {
    padding: 8px;
  }
  
  .dashboard-main {
    grid-template-columns: 1fr;
  }
  
  .metrics-grid, .inspiration-grid {
    grid-template-columns: 1fr;
  }
  
  .jet-cards-container {
    flex-direction: column;
  }
  
  .jet-card {
    min-width: 100%;
  }
}

/* Fix for backdrop-filter support */
@supports not (backdrop-filter: blur(12px)) {
  .dashboard-sidebar,
  .metric-card,
  .chart-card,
  .next-flight-card,
  .jet-showcase-card,
  .map-card,
  .chat-card,
  .inspiration-card {
    background: rgba(255, 255, 255, 0.95);
  }
}

/* Fix for grid layout in older browsers */
@supports not (display: grid) {
  .dashboard-main,
  .metrics-grid,
  .middle-grid,
  .inspiration-grid {
    display: flex;
    flex-wrap: wrap;
  }
}

/* Add print styles */
@media print {
  .dashboard-container {
    background: none;
    box-shadow: none;
  }
  
  .dashboard-sidebar {
    display: none;
  }
  
  .metric-card,
  .chart-card,
  .next-flight-card,
  .jet-showcase-card,
  .map-card,
  .chat-card,
  .inspiration-card {
    break-inside: avoid;
    box-shadow: none;
    border: 1px solid #ccc;
  }
}